
.footer{
  width: 100%;
  min-height: 30vh;
  position: absolute;
  background-color: #ead8ca5d;  
  display: flex;
  justify-content: space-around;
  padding: 8vh;
}
.footer div h3 {
  color: rgba(0, 0, 0, 0.781);
  letter-spacing: 0.1vw;
  margin-bottom: 2vh;
  font-size: 1.4rem;
}
.footer div p {
  color: rgba(0, 0, 0, 0.637);
  padding: 0.5vh;
  letter-spacing: 0.05vw;
  margin-bottom: 1vh;
  font-size: 1.2rem;
}  
footer .socialNetworks {
  flex-direction: row;
}

.footer .infoContact {
  display: flex;
  flex-direction: column;
}  

.footer .iconSocial {
  color:rgba(73, 73, 73, 0.637);
  display:flex;
  align-items: center;
}  
.footer .iconFooter{
  color:rgba(73, 73, 73, 0.637);
  margin-right:1rem;
}

