@media only screen and (max-width: 768px) { 
  .footer{
    min-height: 30vh;
    padding: 4vh;
  }
  div h3 {
    font-size: .7rem;
  }
  div p {
    font-size: .9rem;
  }
  div {
    gap: 1;
  }
}

@media (max-width: 425px){
  div h3 {
    margin-bottom: 1vh;
    font-size: 2rem;
  }

}