/*DIV PADRE */
.gatewayContainer {
  display: flex;
  justify-content: center;
  justify-content: space-around;
  padding: 1.5vw;
}

/*FORM STYLES */
.checkoutInfo {
  display: flex;
  flex-direction: column;
  width: 55%;
}

.checkoutInfo h1 {
  text-align: center;
  font-size: 4vmin;
  font-family: "Cormorant", serif;
  color: #746966;
  text-transform: uppercase;
  padding: 0.5vw;
  margin: 2vh 0 0.4vh;
}

.inputMail,
.inputDiv {
  display: flex;
  width: 100%;
  padding: 1vw;
  flex-wrap: wrap;
}
.giftCardCode div input,
.inputMail input,
.inputDiv div input {
  flex: 1;
  margin-left: 0.5vw;
  padding-left: 0.5vw;
  border: none;
  border-bottom: 1px solid #757575;
  font-size: 3vmin;
}
.inputDiv div {
  flex: 1;
}
.giftCardCode div input:focus,
.inputMail input:focus,
.inputDiv div input:focus {
  outline: none;
}
.inputDiv div {
  display: flex;
}
.giftCardCode label,
.inputDiv label,
.inputMail label {
  font-size: larger;
  color: #000000be;
  margin-left: 1vw;
}

.checkOut {
  width: 50%;
  margin: 1vw;
}

.error {
  text-align: center;
  font-size: medium;
  color: rgb(216, 34, 34);
}
.giftCardText {
  text-align: center;
  font-size: large;
  padding: 1.5vw;
  margin: 2vh 0;
  color: #000000be;
  border: 1px solid black;
}
.giftCardText p {
  margin: 2vh 0;
}

.giftCardText span {
  font-weight: bold;
  color: black;
}

.paywithGiftC {
  display: flex;
  flex-direction: column;
}

/*INPUT ANIMATION */
/* Input bars on focus */
.bar {
  position: relative;
  display: block;
  width: 98%;
  margin-left: 0.5vw;
}
.bar:before,
.bar:after {
  content: "";
  height: 2px;
  width: 0;
  bottom: 1px;
  position: absolute;
  background: #bf988f;
  transition: 0.2s ease all;
  -moz-transition: 0.2s ease all;
  -webkit-transition: 0.2s ease all;
}
.bar:before {
  left: 50%;
}
.bar:after {
  right: 50%;
}

.inputDiv div div {
  display: flex;
  flex-direction: column;
}

.giftCardCode {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.giftCardCode div {
  width: 30%;
}

.giftCardCode div input {
  width: 100%;
}

/* Active input */

.giftCardCode div input:focus ~ .bar:before,
.giftCardCode div input:focus ~ .bar:after,
.inputDiv div div input:focus ~ .bar:before,
.inputDiv div div input:focus ~ .bar:after {
  width: 50%;
}

/*RADIO BUTTONS STYLES */
.switchField {
  margin: 2vw;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.switchField input {
  position: absolute !important;
  clip: rect(0, 0, 0, 0);
  height: 20px;
  width: 20px;
  border: 0;
  margin: 1vw;
}
.giftCardCode button,
.switchField label {
  background-color: #ead8ca94;
  color: #000000be;
  font-size: larger;
  font-weight:500;
  text-align: center;
  padding: 2vh;
  margin: 2vh;
  border: 1px solid rgba(0, 0, 0, 0.2);
  box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.3), 0 1px rgba(255, 255, 255, 0.1);
  transition: all 0.1s ease-in-out;
}

.giftCardCode button,
.switchField label:hover {
  cursor: pointer;
}

.switchField input:checked + label {
  background-color: #dabea8;
  box-shadow: none;
}

.giftCardCode button,
.switchField label {
  border-radius: 4px;
}

.radioButtons h2 {
  text-align: center;
  margin: 1.5vw;
  text-transform: uppercase;
  letter-spacing: 0.3vw;
}
/*CHECKOUT BUTTON STYLES*/
.checkOut {
  display: inline-block;
  position: relative;
  padding: 10px 20px;
  border: 2px solid #ffe4c4;
  color: #000000be;
  font-size: x-large;
  margin: 20px auto;
  transition: color 0.4s;
  text-transform: uppercase;
  background-color: #e4e6e5;
  cursor: pointer;
}
.checkOut:after {
  content: " ";
  border: 2px solid rgba(0, 0, 0, 0);
  display: inline-block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  transition: all 0.4s;
}

.checkOut:hover:after {
  border: 2px solid #ffe4c4;
  width: calc(100% - 10px);
  height: calc(100% + 10px);
}

.alertPago{
  text-align: center;
  margin-top:15vw;
  font-size: 3vmin;
  width: 30vw;
  height: 10vw;
  background-color: #d0d1d1;
  border:2px solid #a3a5a5;
  padding: 1.5vw 5vw;
  border-radius: 15px;
} 

@media only screen and (max-width: 425px){
  .giftCardCode button,
  .switchField label{
    font-size: 2vmin;
  }
}

@media only screen and (max-width: 768px){
  .gatewayContainer {
    flex-direction: column;
    align-items: center;
  }
  .checkoutInfo {
    width: 75%;
  }
  .checkoutInfo h1 {
    font-size: 3.5vmin;
  }
  .inputMail,
  .inputDiv {
    font-size: 2.7vmin;
  }
  .giftCardCode div input,
  .inputMail input,
  .inputDiv div input {
    font-size: 3.5vmin;
  }

  .inputDiv div {
    display: flex;
    width: 100%;
  }
  .giftCardCode label,
  .inputDiv label,
  .inputMail label {
    font-size: 3.5;
  }
  .radioButtons {
    min-height: 5vh;
  }
  .switchField input {
    height: 5vh;
    width: 5vw;
    display: flex;
    flex-wrap: wrap;
  }  
  .switchField {
    margin: 2vw;
    display: flex;
    justify-content: space-evenly;
  }
  .giftCardCode button,
  .switchField label {
    margin-top: 1vh;
  }
  .checkOut {
    font-size: 3vmin;
  }
  .totalPrice768 a button{
      display: block;
      background: #d4c5bf;
      border: 0;
      color: #7e6661;
      padding: 1.5vh;
      font-size: 3vmin;
      font-weight: 300;
      width: 100%;
      cursor: pointer;
      -webkit-transition: all 0.4s;
      transition: all 0.4s;
      border-radius: 2px;
    }
    .productInfo button:active {
      background: #d4c5bf;
      color: #263238;
    }
    .productInfo button:hover {
      background: #bf988f;
      color: #fff;
      -webkit-transition: all 0.4s;
      transition: all 0.4s;
    }
}

@media only screen and (min-width: 769px){
  .totalPrice768{
    display: none;
  }
}